.profile__name {
  max-width: 294px;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px; /* or 114% */
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
@media screen and (max-width: 1101px) and (min-width: 320px) {
  .profile__name {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}